import Page from '../../components/page'
import Layout from '../../components/layout'
import Post from '../../components/post'
import { Container, Row, Col } from 'reactstrap';

import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/styles/hljs';

import Link from 'next/link'

export class Title extends React.Component {
    render() {	return (<h4> מה יש בריפו ומה אין בריפו </h4>)    }
}

export class PreviewContents extends React.Component {
    render() {
	
	return (<p>
	    אם נניח, במקרה, <br/>
באופן ספוראדי שכזה, <br/>
תרצה לרגע לגלות <br/>
מה עלול שמור להיות<br/>
בריפוזיטורי בגיט<br/>
ומה בחוץ ללא תכלית<br/>
</p>)
    }
}


export default class extends Post {
    preview() {
	return (<Container>
		<Title/>
		<PreviewContents/>
		<Link href={this.state.path}>Read more..</Link>
		</Container>)
    }
    post() {
	return (<Layout dir="rtl" {...this.props} navmenu={false}>
		<Title/>
		<PreviewContents/>		

<p> 
או אז תבוא לעזרתך הפקודה הנפלאה<br/>
שמבצעת השוואה מדוקדקת ונכונה<br/>
	בין מה ששמור בפנים ובין מה שנשכח .. דביל.<br/>
		</p>
		<SyntaxHighlighter wrapLines="1" language='bash' style={docco}>{`
# מצא את כל קבצי הגיט
(git ls-files | sort > /tmp/tracked.txt) && \\
# ואת כל הקבצים בכלל
(find . -type f ! -wholename '*node_modules/*' ! -wholename '*/.next/*' ! -wholename '*.git*' | \\
cut -f2- -d'/' | sort > /tmp/all.txt) && \\
# והשווה בין שתי הרשימות, פלוט רק את מה שלא שמור בגיט
comm -23 /tmp/all.txt /tmp/tracked.txt
`}</SyntaxHighlighter>
		<p> נא לשים לב שתחת wholename מתעלמים מכל מה שבאמת-באמת לא צריכים.</p>
		</Layout>)
	
    }
}



